body.dev:before,
body.desarrollo:before, body.dev .ed-container:before,
body.desarrollo .ed-container:before, body.dev .ed-item:before,
body.desarrollo .ed-item:before {
  content: attr(class);
  font-style: italic;
  font-size: 0.75rem;
  font-weight: normal;
  z-index: 1;
  right: 0;
  position: absolute;
  display: table;
  width: 100%;
  padding: 0 0.3125em;
  height: 1.25rem;
  line-height: 1.25rem;
}

body.dev,
body.desarrollo {
  margin: 0 !important;
  position: relative;
  top: 66px;
}

body.dev:before,
body.desarrollo:before {
  position: fixed;
  background: steelblue;
  color: #FFF;
  font-size: 0.875rem;
  text-align: center;
  line-height: 36px;
  height: 36px;
  top: 0;
}

body.dev:before,
body.desarrollo:before {
  content: "ed-grid: size s, from 0 (ed-containers: red, ed-items: blue, yellow)";
}

