  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.ed-video {
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
}

.ed-video > iframe,
.ed-video > video,
.ed-video > .video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ed-menu.s-horizontal {
  width: 100%;
  display: flex;
  align-items: center;
}

.ed-menu.s-horizontal, .ed-menu.s-horizontal ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.ed-menu.s-horizontal li {
  position: relative;
  list-style: none;
  flex: auto;
}

.ed-menu.s-horizontal li.parent-submenu {
  display: flex;
  flex-wrap: wrap;
}

.ed-menu.s-horizontal li.parent-submenu a {
  flex: auto;
}

.ed-menu.s-horizontal ul {
  display: none;
}

.ed-menu.s-horizontal ul.show-submenu {
  display: block;
  width: 100%;
  order: 3;
  margin-left: 1em;
  margin-bottom: .5em;
}

.ed-menu.s-horizontal a {
  display: block;
  line-height: 3em;
  padding: 0 1em;
}

.ed-menu.s-horizontal a:hover {
  text-decoration: none;
}

.ed-menu.s-horizontal a {
  text-align: center;
}

.ed-menu.s-horizontal .expand {
  display: none;
}

.ed-menu.s-horizontal ul {
  position: absolute;
  left: 0;
  top: 100%;
  min-width: 100%;
  white-space: nowrap;
}

.ed-menu.s-horizontal ul a {
  text-align: left;
}

.ed-menu.s-horizontal ul ul {
  top: 0;
  left: 100%;
}

.ed-menu.s-horizontal li:hover > ul {
  display: block;
}

.ed-menu.s-horizontal .expand-submenu {
  display: none;
  position: relative;
  right: 0;
  width: 3em;
  height: 3em;
  cursor: pointer;
  transition: all .3s;
}

.ed-menu.s-horizontal .expand-submenu::after {
  content: "";
  position: absolute;
  width: 40%;
  height: 40%;
  top: 20%;
  left: 30%;
  border-left: 0.45em solid;
  border-bottom: 0.45em solid;
  border-radius: 0.24em;
  transform: rotate(-45deg);
  transition: all .3s;
}

.ed-menu.s-horizontal .expand-submenu.active {
  transform: rotate(180deg);
}

.ed-menu.m-horizontal {
  width: 100%;
