    margin-left: 4rem;
  }
}

blockquote::before {
  content: "\201C";
  font-family: Georgia, serif;
  font-size: 3em;
  position: absolute;
  top: .5em;
  left: -.6em;
  line-height: 0;
  letter-spacing: -0.5em;
}

a {
  color: var(--second-color);
}

main a {
  text-decoration: none;
}

main a:hover {
  text-decoration: none;
}

::selection {
  background: var(--second-color);
  color: #FFF;
}

.underline-title {
  border-bottom: 4px solid var(--fourth);
  display: block;
  width: fit-content;
  color: var(--first);
  text-align: center;
