  }
}

.element-centered {
  margin-left: auto;
  margin-right: auto;
}

body {
  margin: var(--header-height) 0 0 0;
  min-height: calc(100vh - var(--header-height));
  background: var(--body-bg);
  display: flex;
  flex-direction: column;
}

.main-container .container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.main-footer {
  background: var(--first);
  color: #FFF;
  font-size: var(--small-font-size);
  margin-top: auto;
}

.top-nav {
  background: var(--second);
  position: fixed;
  top: 0;
  width: 100%;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 5;
}

@media only screen and (min-device-width: 0px) and (max-device-width: 960px) and (-webkit-min-device-pixel-ratio: 2) {
  .top-nav {
    display: none;
  }
}

.top-nav__mobile {
  position: fixed;
  top: 15px;
  width: 105px;
  background-image: linear-gradient(to right bottom, #b61827, #941047, #652151, #372546, #1b1d29);
  z-index: 4;
  padding: 6px 16px 6px 20px;
  border-radius: 0 6px 6px 0;
}

@media screen and (min-width: 960px) {
  .top-nav__mobile {
    display: none;
  }
}

.top-nav__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--header-height);
  width: 100%;
  padding: 0 1rem;
}

@media screen and (min-width: 960px) {
  .top-nav__container {
    padding: 0 1rem;
  }
}

@media screen and (min-width: 1600px) {
  .top-nav__container {
    padding: 0;
  }
}

.top-nav__link {
  align-self: center;
}

.top-nav__logo {
  height: 25px;
  width: auto;
  display: block;
}

.top-nav__title {
  display: none;
  color: var(--first);
  text-transform: uppercase;
  margin: 0;
  border-bottom: 1px inset var(--fourth);
  padding-bottom: 4.5px;
}

@media screen and (min-width: 960px) {
  .top-nav__title {
    display: inline-block;
  }
}

.top-nav__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  padding: 0 10px;
  height: 100vh;
  width: 100%;
  background: var(--second);
  transition: right .5s;
  overflow-y: scroll;
  top: var(--header-height);
  right: -100%;
}

.top-nav__menu.show-top-nav {
  right: 0;
}

.top-nav__menu-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin-bottom: 32px;
}

.top-nav__menu-link {
  color: var(--first);
  display: flex;
  padding: 18px 0;
  margin: 0;
  margin-left: 1em;
  list-style-type: none;
  text-decoration: none;
  flex-direction: row;
}

.top-nav__menu-link img {
  margin-right: 10px;
}

.top-nav__anchor {
  color: var(--first);
  display: flex;
  flex-direction: row;
  font-weight: 600;
  align-items: center;
}

.top-nav__mb-iconMenu {
  position: relative;
  align-self: center;
  top: 3px;
  padding: 0;
}

@media screen and (min-width: 960px) {
  .top-nav__mb-iconMenu {
    display: none;
  }
}

.top-nav__mb-iconMenu span.hamburger-inner {
  background: var(--first);
}

.top-nav__mb-iconMenu span.hamburger-inner::after, .top-nav__mb-iconMenu span.hamburger-inner::before {
  background: var(--first);
}

.top-nav__mb-iconMenu span.hamburger-inner:active {
  background-color: var(--first);
}

.top-nav__mb-iconMenu:focus {
  outline: 0 !important;
  -moz-outline-style: none !important;
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

.top-nav__social {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
}

.top-nav__social__img {
  width: 33px;
  margin-bottom: 8px;
}

.top-nav__social__text {
  margin-bottom: 0;
  font-family: var(--secondary-font-noto);
  font-size: 16px;
  color: var(--first);
}

.top-nav__desktop {
  background-color: var(--seventh);
  opacity: 93;
  width: 100%;
  display: none;
  height: 56px;
}

@media screen and (min-width: 960px) {
  .top-nav__desktop {
    display: block;
    padding: 0 15px;
  }
}

.top-nav__desktop-list {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1320px;
  padding: 0;
  margin-bottom: 0;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}

.top-nav__desktop-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 18px;
  padding-top: 14px;
  box-sizing: border-box;
  position: relative;
}

.top-nav__desktop-link .rotate-center {
  margin-right: 8px;
}

@-webkit-keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.top-nav__desktop-link::after {
  background-color: var(--fourth);
  content: '';
  display: block;
  height: 0.125rem;
  transition: width .3s;
  width: 0;
  position: absolute;
  top: 53px;
}

.top-nav__desktop-link:hover::after {
  transition: width .3s;
  width: 100%;
}

.top-nav__desktop-link:hover a.top-nav__anchor {
  color: var(--ninth);
}

.top-nav__desktop-link:hover .rotate-center {
  -webkit-animation: rotate-center 0.6s ease-out both;
  animation: rotate-center 0.6s ease-out both;
}

.top-nav-control {
  display: flex;
}

